<template>
  <div class="nft-market">
    <div class="nft-market__container">
      <h1 class="nft-market__title">NFT Marketplace</h1>
      
      <!-- Секция для создания листинга -->
      <div v-if="hasWallet" class="nft-market__create-section">
      <!-- <div v-if="true" class="nft-market__create-section"> -->
        <h2 class="nft-market__section-title">Sell Your NFT</h2>
        <p class="nft-market__section-description">List your NFT for sale on our marketplace and earn SOL.</p>
        <button 
          @click="showCreateListingModal = true" 
          class="nft-market__create-button"
        >
          Create Listing
        </button>
      </div>
      
      <!-- Секция для отображения активных листингов -->
      <div class="nft-market__listings-section">
        <div class="nft-market__listings-header">
          <h2 class="nft-market__section-title">Active Listings</h2>
          <div class="nft-market__listings-controls">
            <button 
              @click="loadListings" 
              class="nft-market__refresh-button"
              :disabled="isLoading"
            >
              <i class="fas fa-sync-alt nft-market__refresh-icon"></i> Refresh
            </button>
            <div v-if="hasWallet" class="nft-market__tab-buttons">
              <button 
                @click="activeTab = 'all'" 
                class="nft-market__tab-button"
                :class="{'nft-market__tab-button--active': activeTab === 'all'}"
              >
                All
              </button>
              <button 
                @click="activeTab = 'my'" 
                class="nft-market__tab-button"
                :class="{'nft-market__tab-button--active': activeTab === 'my'}"
              >
                My Listings
              </button>
            </div>
          </div>
        </div>
        
        <!-- Загрузка -->
        <div v-if="isLoading" class="nft-market__loader-container">
          <div class="nft-market__loader"></div>
        </div>
        
        <!-- Нет листингов -->
        <div v-else-if="displayedListings.length === 0" class="nft-market__empty-state">
          <p class="nft-market__empty-title">No NFT listings found.</p>
          <p v-if="activeTab === 'my'" class="nft-market__empty-subtitle">
            You haven't listed any NFTs for sale yet.
          </p>
          <p v-else class="nft-market__empty-subtitle">
            Be the first to list your NFT on our marketplace!
          </p>
        </div>
        
        <!-- Сетка листингов -->
        <div v-else class="nft-market__listings-grid">
          <NftListingCard 
            v-for="listing in displayedListings" 
            :key="listing.id" 
            :listing="listing"
            :wallet-address="walletAddress"
            :connected="hasWallet"
            @view="viewListing"
            @bid="placeBid"
            @cancel="cancelListing"
          />
        </div>
      </div>
    </div>
    
    <!-- Модальные окна -->
    <CreateListingModal 
      v-if="showCreateListingModal" 
      :user-nfts="userNfts"
      :is-loading="isLoadingUserNfts"
      :is-submitting="isCreatingListing"
      @close="showCreateListingModal = false"
      @submit="handleCreateListing"
    />
    
    <PlaceBidModal 
      v-if="showBidModal && selectedListing" 
      :listing="selectedListing"
      :is-submitting="isPlacingBid"
      @close="showBidModal = false"
      @submit="handlePlaceBid"
    />
    
    <CancelListingModal 
      v-if="showCancelModal && selectedListing" 
      :is-submitting="isCancelling"
      @close="showCancelModal = false"
      @confirm="handleCancelListing"
    />
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from 'vue';
import { useWallet } from 'solana-wallets-vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import axios from 'axios';
import { Connection, PublicKey, Transaction } from '@solana/web3.js';
import { Metaplex, walletAdapterIdentity, auctionHouseModule, sol } from '@metaplex-foundation/js';

import NftListingCard from '@/components/nft-market/NftListingCard.vue';
import CreateListingModal from '@/components/nft-market/CreateListingModal.vue';
import PlaceBidModal from '@/components/nft-market/PlaceBidModal.vue';
import CancelListingModal from '@/components/nft-market/CancelListingModal.vue';

export default {
  name: 'NftMarketPage',
  components: {
    NftListingCard,
    CreateListingModal,
    PlaceBidModal,
    CancelListingModal
  },
  computed: {
    hasWallet() {
      return this.$store.getters.hasWallet;
    }
  },
  setup() {
    const router = useRouter();
    const store = useStore();
    const { wallet, connected, publicKey } = useWallet();
    
    // Состояние данных
    const listings = ref([]);
    const myListings = ref([]);
    const userNfts = ref([]);
    const selectedListing = ref(null);
    
    // Состояние UI
    const isLoading = ref(false);
    const isLoadingUserNfts = ref(false);
    const isCreatingListing = ref(false);
    const isPlacingBid = ref(false);
    const isCancelling = ref(false);
    const activeTab = ref('all');
    const showCreateListingModal = ref(false);
    const showBidModal = ref(false);
    const showCancelModal = ref(false);
    
    // Вычисляемые свойства
    const walletAddress = computed(() => {
      return publicKey.value?.toBase58();
    });

    const displayedListings = computed(() => {
      return activeTab.value === 'my' ? myListings.value : listings.value;
    });
    
    // Настройка Metaplex
    const getMetaplex = () => {
      const connection = new Connection(
        'https://sleek-icy-frog.solana-mainnet.quiknode.pro/9d4845e2d4c60caf45dc4f10d0bdcfdbcd6a321c'
      );
      
      // Проверяем, есть ли подключение к кошельку через Vuex
      if (!store.getters.hasWallet) {
        throw new Error('Wallet not connected');
      }
      
      // Используем кошелек из window.solana (PhantomWallet)
      if (!window.solana) {
        throw new Error('Phantom wallet not installed');
      }
      
      // Создаем адаптер для Phantom кошелька
      const phantomWalletAdapter = {
        publicKey: publicKey.value,
        signTransaction: async (transaction) => {
          if (!window.solana) throw new Error('Phantom wallet not installed');
          return await window.solana.signTransaction(transaction);
        },
        signAllTransactions: async (transactions) => {
          if (!window.solana) throw new Error('Phantom wallet not installed');
          return await window.solana.signAllTransactions(transactions);
        },
        signMessage: async (message) => {
          if (!window.solana) throw new Error('Phantom wallet not installed');
          return await window.solana.signMessage(message);
        }
      };
      
      return Metaplex.make(connection)
        .use(walletAdapterIdentity(phantomWalletAdapter))
        .use(auctionHouseModule());
    };
    
    // Получение Auction House
    const getAuctionHouse = async () => {
      const metaplex = getMetaplex();
      return await metaplex.auctionHouse().findByAddress({
        address: new PublicKey('G26L5YGdj9PaYuePz2jj7Qez2S3PJ39xGEKbAnoYHfVE')
      });
    };
    
    // Методы для загрузки данных
    const loadListings = async () => {
      try {
        isLoading.value = true;
        const response = await axios.get('/nft-market/listings');
        listings.value = response.data.data;
      } catch (error) {
        console.error('Error loading listings:', error);
      } finally {
        isLoading.value = false;
      }
    };
    
    const loadMyListings = async () => {
      if (!store.getters.hasWallet) return;
      
      try {
        const response = await axios.get('/nft-market/listings/my');
        myListings.value = response.data.data;
      } catch (error) {
        console.error('Error loading my listings:', error);
      }
    };
    
    const loadUserNfts = async () => {
      if (!store.getters.hasWallet) return;
      
      try {
        isLoadingUserNfts.value = true;
        const response = await axios.get('/nft-market/my-nfts');
        // Адаптируем формат данных для компонента
        userNfts.value = response.data.data.map(nft => ({
          mintAddress: nft.assetPublicKey,
          imageUrl: nft.imageUri,
          name: nft.name,
          id: nft.id
        }));
      } catch (error) {
        console.error('Error loading user NFTs:', error);
        userNfts.value = [];
      } finally {
        isLoadingUserNfts.value = false;
      }
    };
    
    // Обработчики событий
    const viewListing = (listing) => {
      router.push(`/nft-market/listing/${listing.mintAddress}`);
    };
    
    const placeBid = (listing) => {
      selectedListing.value = listing;
      showBidModal.value = true;
    };
    
    const cancelListing = (listing) => {
      selectedListing.value = listing;
      showCancelModal.value = true;
    };
    
    const handleCreateListing = async (data) => {
      try {
        isCreatingListing.value = true;
        
        if (!store.getters.hasWallet) {
          alert('Please connect your wallet first.');
          isCreatingListing.value = false;
          return;
        }
        
        const initialPrice = parseFloat(data.initialPrice);
        console.log(`Creating listing for NFT: ${data.nft.mintAddress} at ${initialPrice} SOL`);

        try {
          // Инициализация Metaplex и подготовка базовых переменных
          const metaplex = getMetaplex();
          const connection = metaplex.connection; 
          const auctionHouse = await getAuctionHouse();
          const wallet = publicKey.value;
          
          console.log('Creating listing using Metaplex SDK...');
          
          // Создаем листинг, используя высокоуровневый API
          // Передаем только необходимые параметры
          console.log(data.nft.mintAddress)
          const myNfts = await metaplex.nfts().findAllByOwner({
              owner: metaplex.identity().publicKey
          });
          // const nfts = await metaplex.nfts().findAllByCreator({ creator: new PublicKey("HhkuTj7DJ75gezohXmG9ZzRpKfb1XydJfeCxXcGQkQxi") });
          // const nft = await metaplex.nfts().findByMint({ mintAddress: data.nft.mintAddress });
            console.log('NFT found:', myNfts);
          try {
            
            
            const { listing, confirmResponse } = await metaplex
              .auctionHouse()
              .list({
                auctionHouse,
                mintAccount: new PublicKey(data.nft.mintAddress),
                price: sol(initialPrice),
                token: 1,
                tokenAccount: new PublicKey('TokenkegQfeZyiNwAJbNbGKPFXCWuBvf9Ss623VQ5DA'),
                // Не используем tokenAccount, т.к. SDK должен сам найти нужный аккаунт
                printReceipt: true // Для получения подтверждения и подписи транзакции
              });
            
            console.log('Listing created successfully:', listing);
            console.log('Transaction confirmed with signature:', confirmResponse.signature);
            
            // Создаем запись о листинге в базе данных
            console.log('Creating listing record in database...');
            const createResponse = await axios.post('/nft-market/listings', {
              mintAddress: data.nft.mintAddress,
              initialPrice: initialPrice,
              instantPrice: data.instantPrice,
              endTime: new Date(data.endDate).toISOString(),
              transactionSignature: confirmResponse.signature
            });
            
            if (createResponse.data.success) {
              console.log('Listing created successfully in database:', createResponse.data);
              
              // Закрываем модальное окно и обновляем списки
              showCreateListingModal.value = false;
              await loadListings();
              await loadMyListings();
            } else {
              console.error('Error creating listing in database:', createResponse.data);
              alert('Failed to create listing in database: ' + createResponse.data.message);
            }
          } catch (sdkError) {
            console.error('Error using Metaplex SDK:', sdkError);
            
            // Пробуем альтернативный подход через получение инструкций
            console.log('Building transaction manually using instructions...');
            
            // Получаем инструкции для создания листинга
            const listInstructions = await metaplex
              .auctionHouse()
              .builders()
              .list({
                auctionHouse,
                mintAccount: new PublicKey(data.nft.mintAddress),
                price: sol(initialPrice)
              })
              .getInstructions();
            
            // Создаем новую транзакцию с инструкциями
            const { Transaction } = await import('@solana/web3.js');
            const tx = new Transaction();
            listInstructions.forEach(ix => tx.add(ix));
            
            // Настройка блокхеша и плательщика комиссии
            const { blockhash, lastValidBlockHeight } = await connection.getLatestBlockhash();
            tx.recentBlockhash = blockhash;
            tx.feePayer = wallet;
            
            console.log('Transaction built, sending to Phantom for signing...');
            
            // Подписываем транзакцию через Phantom
            if (!window.solana) {
              throw new Error('Phantom wallet not installed');
            }
            
            const signedTx = await window.solana.signTransaction(tx);
            
            // Отправляем подписанную транзакцию
            const signature = await connection.sendRawTransaction(
              signedTx.serialize(),
              { skipPreflight: false }
            );
            
            console.log('Transaction sent with signature:', signature);
            
            // Ждем подтверждения
            const confirmationResult = await connection.confirmTransaction({
              signature,
              blockhash,
              lastValidBlockHeight
            });
            
            if (confirmationResult.value && confirmationResult.value.err) {
              throw new Error(`Transaction failed: ${confirmationResult.value.err}`);
            }
            
            console.log('Transaction confirmed successfully');
            
            // Создаем запись о листинге в базе данных
            console.log('Creating listing record in database...');
            const createResponse = await axios.post('/nft-market/listings', {
              mintAddress: data.nft.mintAddress,
              initialPrice: initialPrice,
              instantPrice: data.instantPrice,
              endTime: new Date(data.endDate).toISOString(),
              transactionSignature: signature
            });
            
            if (createResponse.data.success) {
              console.log('Listing created successfully in database:', createResponse.data);
              
              // Закрываем модальное окно и обновляем списки
              showCreateListingModal.value = false;
              await loadListings();
              await loadMyListings();
            } else {
              console.error('Error creating listing in database:', createResponse.data);
              alert('Failed to create listing in database: ' + createResponse.data.message);
            }
          }
        } catch (listingError) {
          console.error('Failed to create listing:', listingError);
            
          // Выводим подробную информацию в консоль для отладки
          const errorMessage = listingError.message || 'Unknown error';
          console.error('Error details:', errorMessage);
            
          // Проверка, является ли это ошибкой инициализации аккаунта
          if (errorMessage.includes('AccountNotInitialized')) {
            alert('Ошибка листинга NFT: Аккаунт не инициализирован. Убедитесь, что NFT действительно принадлежит вам.');
          } else {
            alert('Не удалось создать листинг: ' + errorMessage);
          }
        }
      } catch (error) {
        console.error('Error creating listing:', error);
        alert('Failed to create listing. Please try again: ' + (error.message || 'Unknown error'));
      } finally {
        isCreatingListing.value = false;
      }
    };
    
    const handlePlaceBid = async (data) => {
      try {
        isPlacingBid.value = true;
        
        if (!store.getters.hasWallet) {
          alert('Please connect your wallet first.');
          isPlacingBid.value = false;
          return;
        }
        
        const bidAmount = parseFloat(data.amount);
        console.log(`Placing bid for NFT: ${data.mintAddress} at ${bidAmount} SOL`);

        try {
          // Используем Metaplex SDK для создания ставки
          console.log('Creating bid via Metaplex SDK...');
          const metaplex = getMetaplex();
          const auctionHouse = await getAuctionHouse();
          
          const { bid, confirmResponse } = await metaplex.auctionHouse().bid({
            auctionHouse,
            mintAccount: new PublicKey(data.mintAddress),
            price: bidAmount,
          });
          
          console.log('Bid created successfully:', bid);
          console.log('Transaction confirmed with signature:', confirmResponse.signature);
          
          // Обновляем информацию о ставке на сервере
          console.log('Recording bid in database...');
          const bidResponse = await axios.post('/nft-market/bid', {
            mintAddress: data.mintAddress,
            bidAmount: bidAmount,
            transactionSignature: confirmResponse.signature
          });
          
          if (bidResponse.data.success) {
            console.log('Bid recorded successfully in database:', bidResponse.data);
            
            // Закрываем модальное окно и обновляем списки
            showBidModal.value = false;
            selectedListing.value = null;
            await loadListings();
            await loadMyListings();
          } else {
            console.error('Error recording bid in database:', bidResponse.data);
            alert('Failed to record bid in database. Please try again.');
          }
        } catch (error) {
          console.error('Error placing bid:', error);
          alert('Failed to place bid. Please try again: ' + (error.message || 'Unknown error'));
        } finally {
          isPlacingBid.value = false;
        }
      } catch (error) {
        console.error('Error placing bid:', error);
        alert('Failed to place bid. Please try again: ' + (error.message || 'Unknown error'));
      } finally {
        isPlacingBid.value = false;
      }
    };
    
    const handleCancelListing = async () => {
      try {
        isCancelling.value = true;
        
        if (!selectedListing.value) {
          alert('No listing selected.');
          isCancelling.value = false;
          return;
        }
        
        if (!store.getters.hasWallet) {
          alert('Please connect your wallet first.');
          isCancelling.value = false;
          return;
        }
        
        const listing = selectedListing.value;
        console.log(`Cancelling listing for NFT: ${listing.mintAddress}`);
        
        // Используем Metaplex SDK для отмены листинга
        const metaplex = getMetaplex();
        const auctionHouse = await getAuctionHouse();
        
        const { confirmResponse } = await metaplex.auctionHouse().cancelListing({
          auctionHouse,
          listing: {
            price: parseFloat(listing.initialPrice),
            mintAccount: new PublicKey(listing.mintAddress),
            seller: publicKey.value,
          }
        });
        
        console.log('Listing cancelled successfully with signature:', confirmResponse.signature);
        
        // Обновляем статус листинга на сервере
        console.log('Updating listing status in database...');
        const cancelResponse = await axios.post('/nft-market/listings/cancel', {
          mintAddress: listing.mintAddress,
          transactionSignature: confirmResponse.signature
        });
        
        if (cancelResponse.data.success) {
          console.log('Listing cancelled successfully in database:', cancelResponse.data);
          
          // Закрываем модальное окно и обновляем списки
          showCancelModal.value = false;
          selectedListing.value = null;
          await loadListings();
          await loadMyListings();
        } else {
          console.error('Error cancelling listing in database:', cancelResponse.data);
          alert('Failed to cancel listing in database. Please try again.');
        }
      } catch (error) {
        console.error('Error cancelling listing:', error);
        alert('Failed to cancel listing. Please try again: ' + (error.message || 'Unknown error'));
      } finally {
        isCancelling.value = false;
      }
    };
    
    // Жизненный цикл
    onMounted(async () => {
      await loadListings();
    });
    
    watch(connected, async (newValue) => {
      if (newValue) {
        await loadMyListings();
      } else {
        myListings.value = [];
        activeTab.value = 'all';
      }
    });
    
    watch(showCreateListingModal, async (newValue) => {
      if (newValue) {
        await loadUserNfts();
      }
    });
    
    return {
      wallet,
      connected,
      walletAddress,
      listings,
      myListings,
      userNfts,
      selectedListing,
      isLoading,
      isLoadingUserNfts,
      isCreatingListing,
      isPlacingBid,
      isCancelling,
      activeTab,
      showCreateListingModal,
      showBidModal,
      showCancelModal,
      displayedListings,
      loadListings,
      loadMyListings,
      loadUserNfts,
      viewListing,
      placeBid,
      cancelListing,
      handleCreateListing,
      handlePlaceBid,
      handleCancelListing,
    };
  }
};
</script>

